import React, { useState } from 'react';
import Todo from './Todo';

const Form = () => {

    const [todo, setTodo] = useState({})

    const [todos, setTodos] = useState({
    list:[
            {todo: 'Manzanas', cantidad: 5},
            {todo: 'Gaseosas', cantidad: 6},
            {todo: 'Detergente', cantidad: 4},
            {todo: 'Jugo de manzana', cantidad: 6}
        ]
    })

    const handleChange = e => setTodo({ ...todo, [e.target.name]: e.target.value })
    const handleChangeCantidad = e => setTodo({ ...todo, [e.target.name]: e.target.value })
    const handleClick = e => {
        if(Object.keys(todo).length === 0 || todo.todo.trim() === '')
        {
            alert('El campo de nombre de producto no puede estar vacio');
            return
        }if((todo.cantidad).length === 0){
            alert('El campo cantidad no puede estar vacío');
            return
        }if(todo.cantidad<=0){
            alert('El campo cantidad deber ser mayor a 0');
            return
        }
           

        todos.list.push(todo)
        setTodos({
            ...todos
        })
      
      
    }

    const deleteTodo = (indice) => {
        const newTodos = { ...todos }
        newTodos.list.splice(indice, 1)
        setTodos(newTodos)
    }

    return(
        <>
        <form onSubmit={ e => e.preventDefault() }>
            <center>
            <label>Nombre de producto</label> <br />
            <input type="text" name='todo' onChange={ handleChange } />
            <label>Cantidad de producto</label> <br />
            <input type="number" name="cantidad"  onChange = { handleChangeCantidad } />
            <button onClick={ handleClick }>Agregar</button>
            </center>
        </form>
            {
             todos.list.map( (value, index) => (<Todo key={ index } todo={ value } index={ index } deleteTodo={ deleteTodo }/>) )
                
            }
        </>
    )
}

export default Form